@import url(https://fonts.googleapis.com/css2?family=Bungee&display=swap);
@font-face {
  font-family: "NeoEular";
  src: url(/not-a-merge-game/static/media/NeoEuler-VGO00.914c30d7.otf);
}

* {
  font-family: "Bungee", cursive;
}

.title {
  text-align: center;
  position: -webkit-sticky;
  position: sticky;
  margin-top: 0;
  flex-basis: 100%;
}

#shop {
  height: 40vh;
  margin-top: 5vh;
  border-style: solid;
  background-color: #999;
  overflow: auto;
}

#shopItems {
  display: grid;
  grid-template-columns: auto auto;
}

.shopItem {
  display: inline-block;
  margin: 5%;
  border: 0;
  background-color: #bbb;
  text-align: center;
  vertical-align: middle;
}

#inventory {
  height: 40vh;
  margin-top: 5vh;
  border-style: solid;
  background-color: #999;
  overflow: auto;
}

#invItems {
  display: grid;
  grid-template-columns: auto auto auto auto auto;
}

.invItem {
  display: inline-block;
  margin: 5%;
  border: 0;
  background-color: #bbb;
  text-align: center;
  vertical-align: middle;
}

